@import '../../../index.scss';

.menu-container,.menu-container-n { display: none;}

@media only screen and (min-width: 1024px){ 
  .menu-container,.menu-container-n {
    background-color: $white;
    display: initial;
    .menu-wrapper {
      display: flex;
      list-style-type: none;
    }
      .menu-link {
        position: relative;
        border: none;
        background-color: transparent;
        cursor: pointer;
        outline: none;
        margin: 0 30px;
        font-weight: bold;
        text-transform: uppercase;
          &::after,
          &::before {
            content: '';
            display: block;
            position: absolute;
            width: 20%;
            height: 20%;
            border: 2px solid;
            transition: all 0.6s ease;
            border-radius: 2px;
          }
            &::after {
            bottom: 0;
            right: 0;
            border-top-color: transparent;
            border-left-color: transparent;
            border-bottom-color: $blue;
            border-right-color: $blue;
          }
          &::before {
            top: 0;
            left: 0;
            border-bottom-color: transparent;
            border-right-color: transparent;
            border-top-color: $blue;
            border-left-color: $blue;
          }
  
          &:hover::after,
          &:hover::before {
            width: 100%;
            height: 100%;
          }
          a {
              color: $black;
              text-decoration: none;
              width: 100%;
              height: 100%;
              display: block;
              z-index: 1;
              position: relative;
              padding: 10px;
            }
      }
  
      .link-category-wrapper {
           position: relative;
           text-transform: uppercase;
           &:hover .category-menu {
            display: initial;
            display: flex;
            justify-content: center;
            list-style-type: none;
            ul.menu-hover, ul.menu-hover-n {
              list-style-type: none;
              width: 100%;
              height: 100%;
              li {
                border-bottom: $blue 1px solid;
                width: 100%;
                height: 100%;
                height: 50px;
              
  
                a {
                  display: block;
                  width: 100%;
                  height: 100%;
                  color: $black;
                  text-decoration: none;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                    &:hover {
                    cursor: pointer;
                    color: $blue;
                  }
                }
                &:last-child {
                  border-bottom: none;
                }
              }}
           }
              
           .category-menu, .category-menu-n {
            display: none;
            position: absolute;
            top: 40px;
            left: 0;
            width: 100%;
            background-color: $white;
            border: 1px solid $blue;
            z-index: 19836;
              &:hover {
              display: initial;
              }
           }
      }
  }
  
  // night
